@charset "UTF-8";
@import "variables";
@import "../../../sass/func";

.new_recovery_price_wrap {
    margin: 0 pxToRem(30px);
    margin-top: pxToRem(40px);
    .price_title{
        color: #666;
        font-size: pxToRem(28px);
        margin-bottom: pxToRem(18px);
    }
    .price_tip{
        position: relative;
        color: #999;
        font-size: pxToRem(26px);
        margin-top: pxToRem(18px);
        padding-left: pxToRem(10px);
        &:after{
            content: '*';
            color:#ffbf00;
            font-size: pxToRem(28px);
            position: absolute;
            left: 0;
            top:0;
        }
    }
    .new_recovery_price {
        padding-top: pxToRem(46px);
        background-color: #fff;
        .type_price {
            background: #fff;
            font-size: pxToRem(30px);
            color: #333;
            padding-left: pxToRem(158px);
            padding-right: pxToRem(158px);
            .span_1 {
                width: 100%;
                span {
                    display: inline-block;
                    &:first-child {
                        width: 49%;
                        line-height: pxToRem(50px);
                    }
                }
                .span_2 {
                    float: right;
                }
            }
        }
    }
}

.fonter_cl {
    position: fixed;
    bottom: 0;
    left: 50%;
    margin-left: pxToRem(-375px);
    @include background("../images/newRecoveryPrice/bg.png", 750px, 456px);
    text-align: center;
    .txt {
        width: 100%;
        position: absolute;
        bottom: pxToRem(25px);
        p {
            font-size: pxToRem(28px);
            color: #999;
            line-height: pxToRem(50px);
        }
    }
}

.list_content {
    padding-top: pxToRem(26px);
    li {
        font-size: pxToRem(28px);
        color: #333;
        line-height: pxToRem(110px);
        .left_cl {
            display: inline-block;
            padding-left: pxToRem(106px);
            img {
                width: pxToRem(64px);
                height: pxToRem(64px);
                margin-right: pxToRem(26px);
                vertical-align: middle;
            }
        }
        .right_cl {
            display: inline-block;
            //          float:left;
            position: absolute;
            left: pxToRem(472px);
        }
    }
}

